import {mapState, mapGetters} from 'vuex'

export default {
	data() {
		return {
		}
	},
	computed: {
		...mapState(['member']),
		...mapGetters(['isLogin'])
	},
	methods: {
		takein() {
			this.$store.commit('SET_ORDER_TYPE', 'takein')
			uni.switchTab({
				url: '/pages/menu/menu'
			})
		},
		takeout() {
			if(!this.isLogin) {
				uni.navigateTo({url: '/pages/login/login'})
				return
			}
			uni.navigateTo({
				url: "/pages/address/address?is_choose=true"	
			})
		},
		integrals() {
			if(!this.isLogin) {
				uni.navigateTo({url: '/pages/login/login'})
				return
			}
			uni.navigateTo({
				url: '/pages/integrals/integrals'
			})
		},
		packages() {
			uni.navigateTo({
				url: '/pages/packages/index'
			})
		},
		memberCode() {
			if(!this.isLogin) {
				uni.navigateTo({url: '/pages/login/login'})
				return
			}
			uni.navigateTo({
				url: '/pages/mine/member-code'
			})
		},
		invite() {
			uni.navigateTo({
				url: '/pages/activities/invite'
			})
		}
	}
}
